    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    color: #fbbf24;
    font-family: monospace;
}

body{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    height: 100vh;
    width: 100vw;
}

.content{
    width: 95vw;
    height: 95vh;
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 1fr 4fr;
    border-radius: 2rem;
    background-color: #451a03;
    box-shadow: inset 0px 0px 10px #fbbf24;
    
}

.header{
    display: grid;
    grid-template-columns: 1fr 4.5fr;
    grid-column: 1 / 3;
    align-items: center;
    padding: 2rem;
}

.logo{
    font-size: 2.5rem;
    font-weight: bold;
}

.addNote{
    font-weight: bold;
    font-size: 1rem;
    justify-self: end;
    border: 3px solid #fbbf24;
    border-radius: 5px;
    padding: 1rem;
    background: none;
}

.addNote:hover, #new-proyect:hover{
    background-color: #fbbf2430;
    cursor: pointer;
    
}

input, select,
#cancel-add,
#cancel-edit,
#cancel-proyect{
    background-color: #fbbf2430;
    color: #fbbf24;
    border: 2px solid #fbbf24;
    padding: 0.5rem;
    border-radius: 5px;
    margin: 1rem;
}

input:focus-visible,
select:focus-visible{
    outline: none;
}

select:hover{
    cursor: pointer;
}

input[type=submit],
#cancel-add,
#cancel-edit,
#cancel-proyect{
    width: 100%;
}

input[type=submit]:hover,
#cancel-add:hover,
#cancel-edit:hover,
#cancel-proyect:hover{
    font-weight: bold;
    cursor: pointer;
}

label{
    font-weight: bold;
    margin-left: 1rem;
}

::placeholder{
    color: #fbbf2480;
}

#task-adder,
#task-editer,
#proyect-adder{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 2rem;
    background-color: #451a03;
    border-radius: 5px;
}

.sidebar{
    padding: 1rem;
    font-size: 1.2rem;
    
}

ul{
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
}

li{
    display: flex;
    align-items: center;
    border: 2px solid transparent;
    padding: 1rem;
    gap: 1rem;
}

li:hover{
    border: 2px solid #fbbf24;
    background-color: #fbbf2430;
    border-radius: 5px;
    cursor: pointer;
}

#proyect-list{
    overflow-y: scroll;
    max-height: 200px;  
}

.proyect-title{
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding: 1rem;
    color: #fbbf24;
    border-top: 2px solid #fbbf2490;
    
}

#new-proyect{
    font-weight: bold;
    font-size: 1rem;
    border: 2px solid #fbbf24;
    border-radius: 5px;
    padding: 0.5rem;
    background: none;
}

.p-color{
    width: 20px;
    height: 20px;
    border: 2px solid #fbbf24;
    border-radius: 5px;
}

.tasks-container{
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    overflow-y: scroll;
    max-height: 500px;  
    padding: 1rem;
}

.card{
    display: grid;
    grid-template-columns: 25px 1fr 1fr;
    gap: 1rem;
    padding: 1rem;
    border: 2px solid #fbbf24;
    border-radius: 5px;
}

.check{
    grid-row: 1 / 4;
    width: 25px;
    height: 25px;
    align-self: baseline;
    justify-self: center;
}

.card-title{
    grid-column: 2 / 3;
    font-size: 1.5rem;
    font-weight: bold;
}

.description{
    grid-column: 2 / 4;
}

.extra-info{
    display: flex;
    align-items: end;
    justify-content: space-between;
}

.extra-info div{
    display: flex;
    align-items: center;
    gap: 1rem;
}

.card-btns{
    display: flex;
    gap: 1rem;
    grid-row: 1;
    grid-column: 3 / 4;
    justify-self: end;
}

.task-edit:hover, 
.task-delete:hover{
    cursor: pointer;
}

.task-done{
    text-decoration: line-through;
}

.tasks-container::-webkit-scrollbar,
#proyect-list::-webkit-scrollbar{
    display: none;
}

.no-tasks{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    height: 400px;
}

.no-tasks img{
    width: 200px;
}

.no-tasks p{
    color: #fbbf2480;
    font-size: 1.5rem;
    font-weight: bold;
}
